.glide__bullet {
  cursor: pointer;
  width: 64px;
  margin: 4px;
  flex: 0 0 64px;
  box-shadow: 0 2px 15px -3px rgba(0,0,0,0.16);
  border-radius: 6px;
  @media screen and (min-width: 475px) {
    flex: 0 0 100px;
    width: 100px;
  }
  @media screen and (min-width: 768px) {
    flex: 0 0 120px;
    width: 120px;
  }
  img {
    display: block;
  }
}
